                            <div class="source" slot="source"><template>
  <div>
  <dt-collapse v-model="activeNames" @change="handleChange">
  <dt-collapse-item title="一致性 Consistency" name="1">
    <dt-tree :data="data" node-key="id">
            <span class="custom-tree-node" slot-scope="{ node, data }">
              <div style="height:22px;line-height:22px;vertical-align: middle;">
                <span v-text="node.label" :title="node.label"></span>
              </div>
            </span>
          </dt-tree>
  </dt-collapse-item>
  <dt-collapse-item title="反馈 Feedback" name="2">
    <div>控制反馈：通过界面样式和交互动效让用户可以清晰的感知自己的操作；</div>
    <div>页面反馈：操作后，通过页面元素的变化清晰地展现当前状态。</div>
  </dt-collapse-item>

  </dt-collapse>
  </div>
</template>
</div>
                            
